	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/bad-holland/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Я уже писала о «<a href=\"/ru/life/good-holland\">хорошей Голландии</a>», а теперь можно и о «плохой». Нужно опять повторить, что Голландия — страна с высоким уровнем жизни, и жить тут на самом деле отлично. Но кое о чём вам лучше бы знать заранее.\n<excerpt/></p>\n<p><strong>Плотность населения</strong></p>\n<p>В Голландии очень высокая плотность населения, самая большая в Европе. Причём это не тенденция последних лет, так здесь было много веков. Поэтому землю экономят: дома стоят плотно друг к другу, улицы узкие, на тротуарах вдвоем не разойтись. Нередка ситуация, когда окна смотрят в окна соседей. В пригороде участки земли около домиков 6*8 метров. Ощущение тесноты везде, разве что в поле или на набережной рек его нету.</p>\n<p><strong>Маленькая страна</strong></p>\n<p>Голландия маленькая. На выбор для жизни буквально 3 города (и все деревни между ними, конечно), которые, в принципе, одинаковые. Разнообразия тут очень не хватает.</p>\n<p>Объездить страну можно за первые месяцы, и потом просто не на что смотреть. Хотя, конечно, есть соседние страны — путешествовать можно туда. Но вот на работу туда уже не устроишься и жить не переедешь.</p>\n<p><strong>Велосипеды</strong></p>\n<p>О значении велосипеда в голландской культуре ходят легенды. Это мимими, когда приезжаешь сюда туристом, но всё меняется, когда начинаешь тут жить.</p>\n<p>Во-первых, великов очень много. Все едут плотным потоком, особенно в центре в час пик. Расслабленной поездка получится только если ваш маршрут пролегает через парк, да и то не всегда.</p>\n<p>Велосипедам тут придается огромное значение, всё сделано для велосипедистов. К сожалению, в ущерб пешеходам. Тротуары очень узкие, потому что за их счет сделана велодорожка. И даже на таком тротуаре пешеход должен постоянно прижиматься к стенке — ведь иногда велосипеды, скутеры и маленькие машинки (они все имеют право ехать по велодорожке) решают выехать на тротуар. Самое неудобное, что они при этом чувствуют себя в своём праве — «велохамство» тут считается нормой.</p>\n<p>Пешеход не может спокойно переходить дорогу на зеленый свет, потому что перед проезжей частью обычно пролегает велодорожка, и на нее никогда не действует светофор. Едущие по ней плотным потоком велосипеды и скутеры по доброй воле не останавливаются. Надо ждать «просвета», и перебегать. Если угол не просматривается, нужно быть особенно осторожным: вылетая из-за поворота на велодорожке тут не принято снижать скорость.</p>\n<p>И это не просто какие-то «недоделки», это осознанная политика. По пути на мою работу, например, пешеходный переход через трассу вообще объединен с велодорожкой. То есть ты переходишь улицу на зеленый свет, а у тебя из подмышки вылетает скутер. И в этот же момент с другой стороны тебя объезжает велик.</p>\n<p>И без того узкие тротуары постоянно заставлены припаркованными мопедами, велосипедами, а иногда и машинами. Нужно выходить на проезжую часть, чтобы обойти их. В некоторых новых районах тротуары пошире, и там все помещаются. Но это скорее исключение.</p>\n<p>Конечно, через полгодика на попе вырастает третий глаз и начинаешь хорошо вилять между этими потоками, но расслабиться здесь нельзя никогда.</p>\n<p><strong>Рабочие моменты для мигрантов</strong></p>\n<p>Для работы в Голландии оформляют собственное разрешение на работу, действительное только здесь, а не Blue Card. Возможность оформить Blue Card тоже есть, но лимит зарплаты для неё очень большой. И самое главное, компании привыкли к местным разрешениям, и действуют по накатанной схеме. С разрешением на работу только в Голландии вы не можете переехать без лишней мороки. А если учесть размеры страны, то получается ещё и ограничение в выборе работы.</p>\n<p>Вообще трудовому мигранту свободы дается не очень много. Kennismigrant может работать только в «умной сфере». Нельзя решить побыть барменом полгодика. Раньше это обязательство было только на первые 3 года, но недавно продлили до 5 лет. Причём от возникновения идеи до принятия закона прошли считанные недели. Точно так же хотят сделать возможным получение гражданства через 7 лет (сейчас через 5), и сделают — я уверена.</p>\n<p><strong>Трудовое законодательство для женщин</strong></p>\n<p>У женщин, приехавших сюда по работе, положение вообще незавидное. Декретный отпуск: 1 месяц до родов и 2 после. Причём продлить его за свой счёт нельзя — просто не будет законных оснований оставаться в стране.</p>\n<p>Работающие мамы сдают тут детей в ясли с 2-3 месяцев, а сами сцеживают молоко в женском туалете в баночки. Из этих баночек ребёнка в яслях на следующий день кормят. Даже писать об этом неловко, не то что примерять такую ситуацию на себя.</p>\n<p>Конечно, пара может переоформить пермиты так, чтобы папа стал «основным» миграном, и тогда мама может оставаться дома с ребёнком. Но возникает другая проблема: 30% ruling они потеряют навсегда. В применении этой налоговой скидки не должно быть перерывов больше 3 месяцев.</p>\n<p><strong>Медицинская система</strong></p>\n<p>Медицинская система в целом хорошая, но довольно платная. Обязательную страховку работающий член семьи должен купить себе и партнеру (детям не надо), а это примерно по 100 евро с человека в месяц. Но если есть такая страховка, это не значит, что будут лечить бесплатно. Есть ещё “франшиза”, то есть при обращении к врачу каждый должен оплатить первые 450 евро в год. Дальнейшие услуги условно по страховке — многие процедуры страховка покрывает на 70%, или есть лимит по сумме.</p>\n<p>Ещё не очень удобно, что медицины в обход страховой системы нет вообще — это нелегально. Если доктор не хочет назначать вам МРТ (а он не будет хотеть если не уверен на 100%, иначе его страховая взгреет), то нигде вы его платно не сделаете. Сами голландцы за платными медицинскими услугами ездят в Бельгию.</p>\n<p><strong>Нормально не поесть</strong></p>\n<p>Культура еды в Голландии меня особенно разочаровала. Там не принято много есть в обед, основной приём пищи — вечером дома с семьёй. А обедают одним бутербродом, в организациях даже перерыв на обед не предусмотрен (долго ли — бутерброд умять). А из-за того что нет спроса на бизнес-ланчи, нет и предложения нормальной еды в кафе. Многие заведения вообще открываются в 5 вечера. А те что открыты раньше в обед предлагают всё равно сэндвичи, будь они на самом деле хоть ресторан высокой кухни. Всё время что я работала в Голландии мне приходилось обедать сэндвичем в Старбаксе. Альтернативой были только бутерброды из супермаркета или японские… тоже сэндвичи.</p>\n<p><strong>Архитектура и жилье</strong></p>\n<p>Голландцы очень бережно относятся к своим домам, именно поэтому Амстердам выглядит как на картинке. На самом деле дом может быть полностью обновлён, но фасад обязательно сохранят. Из-за этого улицы в центре не обновляются — всё те же узкие тротуары и мало зелени (я имею в виду палисадники). Но в центре понятно — всё таки, туристическое место и историческое наследие.</p>\n<p>Однако, такое же отношение и к не очень старому жилью. Застройку середины 20 века тоже сохраняют, а она красотой не блещет — это целые улицы длинных однообразных домов, которые я могла называть только словом “бараки”. На самом деле внутри там всё отлично, но внешний дизайн вызывает вопросы. И ещё практически все дома в одной цветовой гамме. (Говорят, настоящий голландец без труда различает 250 оттенков коричневого.) Получается довольно уныло, и это справедливо для всей Голландии. Немного лучше с архитектурой на юге, например <a href=\"http://varya-daily.tumblr.com/post/83904815572/maastricht\">в Маастрихте</a>.</p>\n<p>Из-за сохранения этих районов, нового жилья не так и много. И оно либо далеко, либо довольно дорого (дороже, чем в самом центре, например). То есть в реальности выбор жилья не самый широкий.</p>","fields":{"slug":"ru/life/bad-holland/","prefix":"","disqusIdentifier":"life-bad-holland-index-ru"},"frontmatter":{"title":"Плохая Голландия","date":"16 May 2014","v2":true,"old":null,"tumblr":true,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/bad-holland/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/again-about-finland/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/again-about-finland/","prefix":"","lang":"ru","disqusIdentifier":"life-again-about-finland-index-ru"},"frontmatter":{"title":"Опять немного про Финлядию"}}}}

